<template>
<!-- <div style="font-size:'50px'">Hello</div> -->
<div id="background">
    <!-- 导航栏开始 -->
    <div class="page-header">
        <div class="w">
            <span class="page-header-logo">LOGO</span>
            <span class="page-header-nav" @click="jumpToHome">首页</span>
            <span class="page-header-nav" @click="jumpToPrivateCloud">私有云</span>
            <span class="page-header-nav">案例</span>
            <span class="page-header-nav" @click="jumpToQA">个性化定制</span>
        </div>
        <!-- 导航栏结束 -->
    </div>
    <div class="questionview" id="entity">
        <div :style="title">
            <div :style="robname">智能云小蜜</div>
            <div :style="verticalLine">|</div>
            <div :style="slogan">在线为您推荐最佳私有云部署方案</div>
        </div>
        <div class="questionHorizontalBox">
            <div class="xiaomi_pic">
                <img :src="pic1" :height="xiaomi_height" />
            </div>
            <div class="questionStyle">
                <p>
                    hi，上午好，小蜜在此等您很久了，享受私有云个性定制，您可
                    <span style="cursor:pointer;color:blue" @click="startQuestion">点此开始</span>，完成在线智能问答!
                </p>
            </div>
        </div>
        <div v-for="(pItem, pIndex) in a" :key="pIndex">
            <div class="questionHorizontalBox">
                <div class="xiaomi_pic">
                    <img :src="pic1" :height="xiaomi_height" />
                </div>
                <div class="questionStyle">
                    <!-- 使用 name 属性定义的单选按钮组 (具有相同名称的单选按钮 属于同一组)。 -->
                    <p>{{pItem.question}}</p>
                    <hr style="height:1px;border:none;border-top:1px solid #5A5A5A; margin-left: 0px;margin-right: 15px" />
                    <div class="radiosGroup">
                        <label class="interval" :class="index == pItem.answers_index ? 'active-btn':''" v-for="(item, index) in pItem.answers.ans_label" :key="index">
                            <input style="display: none" type="radio" name="companyScale" :value="item" @click="clickOn1(pIndex,index,item)" />
                            {{item}}
                        </label>
                    </div>
                </div>
            </div>
            <div class="answerHorizontalBox" v-show="pItem.companyvalue">
                <div class="answerStyle">
                    <p>{{pItem.companyvalue}}</p>
                </div>
                <div class="self_pic">
                    <img :src="pic2" :height="self_height" />
                </div>
            </div>
        </div>
        <div class="questionHorizontalBox" v-show="showBoxcheck">
            <div class="xiaomi_pic">
                <img :src="pic1" :height="xiaomi_height" />
            </div>
            <div class="questionStyle">
                <!-- 使用 name 属性定义的单选按钮组 (具有相同名称的单选按钮 属于同一组)。 -->
                <p>
                    您好，您已成功完成问答，请
                    <span style="cursor:pointer;color:blue" @click="getVerification">点击获取</span>
                    您的私有云部署方案
                </p>
            </div>
        </div>
        <div class="Verification_class" v-show="isEndingShow">
            <div class="phoneNum">
                <span style="margin-top:7px; margin-left:10px">+86</span>
                <input class="input1" type="text" placeholder="手机号码" v-model="registerPhoneNum" />
            </div>
            <div class="testifyNumLine">
                <input class="input2" type="text" placeholder="输入验证码" v-model="registerIdentification" />
                <!--                     <input class="getVerificationNumButton" type="button" value="获取验证码" v-show="show" @click="getVerificationFunc">
          <input class="getVerificationNumButton" type="button" value="count s后重试" v-show="!show" @click="getVerificationFunc">-->
                <span class="getVerificationNumButton" v-if="show" @click="getVerificationFunc">
                    <center>获取验证码</center>
                </span>
                <span v-if="!show" class="getVerificationNumButton">
                    <center>{{count}}s后重试</center>
                </span>
            </div>
            <div class="getReport" @click="getReportFunc">
                <center>获取报告</center>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    visit
} from '../../api'
export default {
    name: "qa",
    data() {
        return {
            pic1: require("../../assets/images/xiaomi.png"),
            xiaomi_height: "50px",
            pic2: require("../../assets/images/self.png"),
            self_height: "50px",

            // companyvalue: "",
            activeCompanyIndex: -1,
            a: [],
            newObj: {
                case_id: -1,
                question: "",
                answers: {
                    ans_id: [],
                    ans_label: [],
                },
                companyvalue: '',
                answers_index: '',
            },
            isEndingShow: false,
            showBoxcheck: false,

            // 验证码计时器
            show: true,
            count: "",
            timer: null,

            registerPhoneNum: "",
            registerIdentification: "",

            title: {
                display: "flex",
                flexDirection: "row",
                borderBottomStyle: "1px solid rgba(53, 63, 99, 1)",
                boxShadow: "2px 2px 5px 1px rgba(182, 187, 196, 0.31)",
                borderRadius: "3px",
            },
            robname: {
                fontSize: "24px",
                color: "# 000 ",
                lineHeight: "41px",
                paddingTop: "10px",
                paddingLeft: "30px",
                fontStyle: "italic",
                fontFamily: "PingFang SC",
            },
            verticalLine: {
                paddingLeft: "20px",
                paddingTop: "20px",
            },
            slogan: {
                paddingLeft: "20px",
                paddingTop: "25px",
                color: "#707070",
                fontSize: "16px",
                fontFamily: "Microsoft YaHei",
            },
            heightLevel: -1,
        }
    },
    updated() {
        let div = document.getElementById("entity")
        div.scrollTop = div.scrollHeight + div.offsetHeight
    },
    mounted() {
        this.active()
    },
    methods: {
        startQuestion() {
            this.isShowQuestion1 = true;
            // this.isEndingShow = true
            this.axios
                .post(`/question`, {
                    ans_id: "0",
                    case_id: '',
                    submit: '1'
                })
                .then((res) => {
                    if (res.status === 200 && res.data.code === 1) {
                        console.log(res.data.data)
                        // 请求成功之后要做的事情
                        this.isShowQuestion1 = true;
                        this.newObj.case_id = res.data.data.case_id;
                        this.newObj.question = res.data.data.question[1];
                        this.newObj.question_num = res.data.data.question[0];
                        // this.newObj.answers = res.data.data.answers;
                        res.data.data.answers.forEach((item) => {
                            this.newObj.answers.ans_id.push(item[0])
                            this.newObj.answers.ans_label.push(item[1])
                        });
                        this.a.push(JSON.parse(JSON.stringify(this.newObj)));
                        // console.log("this.a:", this.a);
                    } else {
                        console.log("else");
                        this.$message({
                            message: "服务器异常",
                            type: "warning",
                        });
                    }
                })
                .catch((err) => {
                    console.log(err);
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                });
            // this.a.push(this.newObj)
        },
        endQuestion() {
            this.$router.push({
                name: "report",
            });
        },

        jumpToHome() {
            this.$router.push({
                name: "home",
            });
        },

        jumpToQA() {
            this.$router.push({
                name: "qa",
            });
        },

        jumpToPrivateCloud() {
            this.$router.push({
                name: "pricloud",
            });
        },
        active() {
            visit({
                params: {
                    "flag": "2"
                },
                success: res => {
                    console.log("访问成功", res)
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        clickOn1(pIndex, index, item) {
            this.a[pIndex].companyvalue = item;
            // answers_index用来在循环中判断点击的是哪一个选项，从而改变该选项的颜色。
            this.a[pIndex].answers_index = index
            this.newObj.answers.ans_id = []
            this.newObj.answers.ans_label = []
            console.log("this.a[pIndex].answers.ans_id[index]:", this.a[pIndex].answers.ans_id[index], typeof (this.a[pIndex].answers.ans_id[index]))
            this.axios
                .post(`/question`, {
                    ans_id: this.a[pIndex].answers.ans_id[index].toString(),
                    case_id: this.a[pIndex].case_id,
                    submit: '1'
                })
                .then((res) => {
                    if (res.status === 200 && res.data.code === 1) {
                        // 请求成功之后要做的事情
                        console.log("ok");
                        console.log("res.data.data:", res.data.data)
                        this.isShowQuestion1 = true;
                        this.newObj.case_id = res.data.data.case_id;
                        this.newObj.question = res.data.data.question[1];
                        this.newObj.question_num = res.data.data.question[0];
                        // this.newObj.answers = res.data.data.answers;
                        res.data.data.answer.forEach((item) => {
                            this.newObj.answers.ans_id.push(item[0])
                            this.newObj.answers.ans_label.push(item[1])
                        });
                        console.log("ans_id:", this.newObj.answers.ans_id)
                        console.log("ans_label:", this.newObj.answers.ans_label)
                        this.a.push(JSON.parse(JSON.stringify(this.newObj)));
                        console.log(this.a, "this.a")

                        // this.newObj.case_id = res.data.data.case_id;
                        // this.newObj.question = res.data.data.question;
                        // this.newObj.answers = res.data.data.answers;
                        // this.newObj.question_num = res.data.data.question_num;
                        // this.a.push(JSON.parse(JSON.stringify(this.newObj)));
                        // console.log(this.a);
                    } else if (res.status === 200 && res.data.code === 0) {
                        console.log("ending");
                        this.showBoxcheck = true;
                    } else {
                        console.log("else");
                        this.$message({
                            message: "服务器异常",
                            type: "warning",
                        });
                    }
                })
                .catch((err) => {
                    console.log(err);
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                });
            this.$emit("clickOn1");
        },

        getVerification() {
            this.isEndingShow = true;
        },

        getVerificationFunc() {
            const TIME_COUNT = 60;
            this.getIdentifyCode();
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000);
            }
        },

        getIdentifyCode() {
            if (this.registerPhoneNum) {
                this.axios
                    .post(`/identify`, {
                        phone_number: this.registerPhoneNum,
                    })
                    .then((res) => {
                        console.log("111");
                        if (res.status === 200 && res.data.code === 0) {
                            console.log("11111");
                            this.$message({
                                message: "正在获取验证码，请稍后",
                                type: "success",
                            });
                        } else {
                            console.log("222");
                            this.$message({
                                message: res.data.msg,
                                type: "warning",
                            });
                        }
                    })
                    .catch((err) => {
                        console.log("333");
                        this.$message({
                            message: err,
                            type: "warning",
                        });
                    });
            } else {
                console.log("444");
                this.$message({
                    message: "请输入手机号码",
                    type: "warning",
                });
            }
        },

        getReportFunc() {
            this.axios
                .post(`/submit`, {
                    case_id: this.a[0].case_id,
                    identify: this.registerIdentification,
                    phone_number: this.registerPhoneNum,
                })
                .then((res) => {
                    console.log("111");
                    if (res.status === 200 && res.data.code === 0) {
                        console.log("11111");
                        this.$message({
                            message: "正在获取报告，请稍后",
                            type: "success",
                        });
                        this.$router.push({
                            name: "report",
                            query: {
                                case_id: this.a[0].case_id,
                                ...this.$route.query //把地址栏之前的query参数也都带上 防止页面丢失数据
                            }
                        });
                    } else {
                        console.log("222");
                        this.$message({
                            message: res.data.msg,
                            type: "warning",
                        });
                    }
                })
                .catch((err) => {
                    console.log("333");
                    this.$message({
                        message: err,
                        type: "warning",
                    });
                });
        },
    },
}
</script>

<style scoped>
