    background-image: url("../../assets/images/qabackground.png");
    background-size: 100% 100%;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

/* 导航栏开始 */
.page-header {
    width: 100vw;
    height: 56px;
    position: fixed;
    /* top: 0;
    left: 0; */
    background-color: rgba(42, 48, 59, 0.5);
    z-index: 1000;
    /* padding-top: 1rem; */
    color: #fff;
    line-height: 56px;
}

.page-header-logo {
    margin-right: 80px;
    font-size: 24px;
}

.page-header-nav {
    margin-right: 32px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}

/* 导航栏结束 */
.questionview {
    border-radius: 6px;
    height: 937px;
    width: 100%;
    /*padding:56px 360px 43px 360px;*/
    background-color: white;
    margin-top: 60px;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    /*    margin-bottom:30px;*/
}

.questionHorizontalBox {
    display: flex;
    margin-top: 20px;
    width: 90%;
    font-size: 14px;
    font-family: Microsoft YaHei;
    color: rgba(24, 24, 24, 1);
    line-height: 15px;
}

.xiaomi_pic {
    /*margin-top: 30px;*/
    margin-left: 15px;
}

.questionStyle {
    width: 900px;
    border-radius: 25px;
    background-color: #f3f3f3;
    margin-left: 12px;
    padding-top: 15px;
    padding-left: 15px;
}

.radiosGroup {
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.interval {
    margin: 5px 5px 5px 5px;
    width: 148px;
    padding: 3px 3px 3px 3px;
}

label {
    padding-top: 3px;
    padding-bottom: 3px;
    min-width: 120px;
    min-height: 39px;
    border: 1px solid rgba(1, 116, 245, 1);
    border-radius: 19px;
    font-family: Microsoft YaHei;
    text-align: center;
    font-size: 16px;
    line-height: 30px;
    color: #0174f5;
}

p {
    font-size: 16px;
    font-family: Microsoft YaHei;
    color: rgba(24, 24, 24, 1);
    margin-block-start: 3px;
}

.answerHorizontalBox {
    display: flex;
    margin-top: 20px;
    width: 100%;
    font-size: 14px;
    font-family: Microsoft YaHei;
    color: rgba(24, 24, 24, 1);
    line-height: 15px;
}

.answerStyle {
    width: 1300px;
    border-radius: 25px;
    background-color: #f3f3f3;
    margin-left: 550px;
    padding-top: 15px;
    padding-left: 15px;
    padding-bottom: 15px;
}

.self_pic {
    margin-right: 15px;
    margin-left: 15px;
}

.active-btn {
    background-color: #0174f5;
    color: #fff;
}

.Verification_class {
    height: 294px;
    width: 493px;
    background-color: #fff;
    margin-left: 400px;
    margin-top: 100px;
    position: absolute;
    z-index: 1000;
}

.phoneNum {
    display: flex;
    height: 40px;
    width: 320px;
    border-radius: 5px;
    border: 1px solid rgba(71, 87, 116, 1);
    opacity: 0.54;
    margin-top: 80px;
    margin-left: 60px;
}

.input1 {
    height: 40px;
    width: 300px;
    border-radius: 5px;
    border: 0px;
    opacity: 0.34;
    margin-left: 10px;
    outline: none;
}

.testifyNumLine {
    margin-top: 15px;
    display: flex;
    margin-bottom: 15px;
}

.input2 {
    height: 40px;
    width: 190px;
    border-radius: 5px;
    border: 1px solid rgba(71, 87, 116, 1);
    opacity: 0.54;
    /*margin-top: 5px;*/
    margin-left: 60px;
    /*margin-bottom: 20px;*/
    outline: none;
}

.getVerificationNumButton {
    height: 40px;
    width: 115px;
    /*margin-top: 5px;*/
    line-height: 40px;
    margin-left: 15px;
    border-radius: 5px;
    border: 1px solid rgba(1, 116, 245, 1);
    background-color: #fff;
    font-size: 14px;
    color: #047aff;
    cursor: pointer;
}

.getReport {
    height: 40px;
    width: 320px;
    margin-left: 60px;
    line-height: 40px;
    border-radius: 5px;
    border: 1px solid;
    background-color: rgba(71, 87, 116, 1);
    /*font-size: 14px;*/
    color: #fff;
    cursor: pointer;
}
</style>
