<template>
<div class="editLeftArea">
    <div class="interval"></div>
    <div class="head">
        <div class="artificalService">
            <img :src="pic1" :height="xiaomi_height" />
            <h3>智能客服</h3>
        </div>
        <div class="editAndSave">
            <button @click="CreateNewQuestion('question')">新增问题</button>
            <button @click="CreateNewQuestion('answer')">新增选项</button>
            <button @click="saveConfig">保存配置</button>
        </div>
    </div>
    <div class="editArea">
        <div id="mountNode" style="height:100%; width:100%; margin:0px;"></div>
        <div class="showTable">
            <div class="labelTextShow" v-if="isShowLabelText">
                <br>
                <p>节点Id:{{this.chooseNodeId}}</p>
                <p>groupId:{{this.chooseNodeGroupId}}</p>
                <br>
                <br>
                <center><textarea cols="15" rows="8" v-model="labelText"></textarea></center>
                <br>
                <center><button @click="showLableTextButtonClick">修改</button></center>
                <center><button @click="recursiveDeleteNode">删除</button></center>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import G6 from '@antv/g6'
export default {
    data() {
        return {
            edgeId: -1,
            postFinishTimes: -1,
            originQuestionX: 250,
            originQuestionY: 60,
            locationBackList: [],
            locationBackUpObj: {
                postFinishTimes: -100,
                groupId: '-100',
                xBackUp: 0,
                yBackUp: 0,
            },
            originQuestionId: '-1',
            chooseNodeGroupId: '-100',
            // nodeOfGroupsList: [],
            originGroupId: 'B',
            originQuestionLabel: '',
            originOptions: [],
            chooseNodeId: -100,
            nodes: [],
            j: 0,
            y: 0,
            repeatClickCnt: 0,
            postFromServerClass_question:'',
            changeQuestionObj: {
                current_id: '',
                new_value: '',
                operation: '',
                question_class: '',
            },
            changeToServeList: [],
            pic1: require("../../assets/images/xiaomi.png"),
            pic2: require("../../assets/images/pencil.jpg"),
            pic3: require("../../assets/images/save.jpg"),
            xiaomi_height: "20px",
            treeGraph: {},
            labelText: '',
            fontLimit: 15,
            fontLimitNewNode: 17,
            isShowLabelText: false,
            newNode: {
                id: '',
                label: '',
                type: 'rect',
                x: -100,
                y: -100,
            },
            canvasHeight: -1,
            canvasWidth: -1,
        }
    },
    mounted() {
        this.postFromQuestion('0', '', 0);
        if (document.documentElement.clientWidth && document.documentElement.clientWidth > 1400) {
            this.canvasWidth = 1532.750;
            this.canvasHeight = 833.766;
        } else {
            this.canvasWidth = 1038.44;
            this.canvasHeight = 540.55;
        }
        this.initGraph();
    },
    watch: {
        // postFinishTimes是用于检测到有请求道数据事件发生的次数变化
        postFinishTimes: function () {
            this.isAnyGroupsExists()
        }
    },
    methods: {
        // 触发post请求后，在画布作画。
        addQuesAndOptionsAuto(x, y, i, j) {
            if (this.treeGraph.findById(this.originQuestionId) == undefined) {
                this.addNodeAuto(this.originQuestionId, this.originQuestionLabel, this.originGroupId, x + 500 * i, y)
            }
            // 因为每个选项的ID都不会重复，所以不影响。需要判断每个节点的groupId，如果groupId相同，则放进一个组里面。
            // 不用再记录每次group的最后一个节点的x, y轴坐标了。
            this.originOptions.forEach((item) => {
                this.addNodeAuto(item[0], item[1], this.originGroupId, x + 500 * i, y + 100 * j)
                if (typeof (this.chooseNodeId) == 'string') {
                    this.addEdge(this.chooseNodeId, item[0])
                }
                j += 1
            });
            this.j = j
            this.y = y
        },
        findNodesOfSameGroupIdInGraph() {
            // 每次请求得到数据后，并没有本地接收，直接画在图里。所以需要找到这些节点。
            let findNodesOfSameGroupId = [];
            this.treeGraph.findAll('node', (node) => {
                if (node.get('model').groupId === this.originGroupId)
                    findNodesOfSameGroupId.push(node)
            })
            this.nodes = [];
            findNodesOfSameGroupId.forEach((item) => {
                this.nodes.push(item._cfg.id)
            })
            // console.log("nodes", this.nodes)
        },
        // 用于判断是否已经存在该组，若已存在,则删除旧的组，并找出重新进入的Node并且生成新group.
        isNeedGroupGenerate(nodes) {
            let existGroupsId = [];
            this.treeGraph.findAll('group', (group) => {
                existGroupsId.push(group.id)
            })
            if (existGroupsId.indexOf(this.originGroupId + 'x') === -1) {
                this.treeGraph.addItem('group', {
                    groupId: this.originGroupId + 'x',
                    nodes,
                    type: 'rect',
                    zIndex: 2,
                    // title: this.originGroupId,
                    title: {
                        text: this.originGroupId,
                        stroke: '#444',
                        fill: '',
                        offsetX: 10,
                        offsetY: 0,
                    }
                })
            } else {
                // console.log("已存在该group分组，ID为:", this.originGroupId)
                this.treeGraph.removeItem(this.originGroupId + 'x')
                this.findNodesOfSameGroupIdInGraph()
                const nodes = this.nodes
                this.treeGraph.addItem('group', {
                    groupId: this.originGroupId + 'x',
                    nodes,
                    type: 'rect',
                    zIndex: 2,
                    // title: this.originGroupId,
                    title: {
                        text: this.originGroupId,
                        stroke: '#444',
                        fill: '',
                        offsetX: 10,
                        offsetY: 0,
                    }
                })
            }

        },
        // 将每一个新组的对象信息postFinishTimes,groupId, xBackUp, yBackUp保存起来。
        storageGroupExistInfo(j) {
            this.locationBackUpObj.postFinishTimes = this.postFinishTimes
            this.locationBackUpObj.groupId = this.originGroupId
            this.locationBackUpObj.xBackUp = this.originQuestionX + 500 * (this.postFinishTimes - this.repeatClickCnt)
            this.locationBackUpObj.yBackUp = this.originQuestionY + 100 * j
            this.locationBackList.push(JSON.parse(JSON.stringify(this.locationBackUpObj)))
        },
        // 要和之前的所有存在locationBackList的对象利用groupId来判断，出现的数据在图中是在以前的某列下面继续出现还是
        // 另起新一行出现。
        isLayoutByRowOrCol() {
            // 将存放组历史的列表中的对象的groupId取出来，并放在locationBackGroupIdList中。
            var locationBackGroupIdList = []
            this.locationBackList.forEach((item) => {
                locationBackGroupIdList.push(item.groupId)
            })

            // 当本次点击的节点是以前groupId的节点时，将向下排列。需要用到item.postFromServerTimes。
            // item.xBackUp, item.yBackUp。

            // 此处用于判断当本次点击的节点是以前groupId的节点时，将向下排列。需要用到item.postFromServerTimes。
            // item.xBackUp, item.yBackUp。

            if (locationBackGroupIdList.indexOf(this.originGroupId) != -1) {

                let item = this.locationBackList[locationBackGroupIdList.indexOf(this.originGroupId)]

                this.addQuesAndOptionsAuto(item.xBackUp, item.yBackUp, 0, 0)

                this.locationBackList[locationBackGroupIdList.indexOf(item.groupId)].yBackUp = this.y + 100 * this.j

                this.findNodesOfSameGroupIdInGraph()

                this.isNeedGroupGenerate()

                this.repeatClickCnt++;
            } else {
                // console.log("新的group出现，出现新的问题和选项")

                this.addQuesAndOptionsAuto(this.originQuestionX, this.originQuestionY, this.postFinishTimes - this.repeatClickCnt, 1)

                this.storageGroupExistInfo(this.j)

                this.findNodesOfSameGroupIdInGraph()

                // // 用于判断是否已经存在该组，若已存在。则不再生成新group，避免使用addItem('group')报错。

                this.isNeedGroupGenerate(this.nodes);

                // this.repeatClickCnt = 0;
            }
        },
        isAnyGroupsExists() {
            // var i = i;
            // 第一次请求时locationBackList为初始值空列表。
            if (this.locationBackList.length === 0) {
                // console.log("~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~")

                this.addQuesAndOptionsAuto(this.originQuestionX, this.originQuestionY, this.postFinishTimes, 1);

                // 把mounted里面post的值的group的groupId, postServerTimes, x, y值当作存入对象中并放在记录组历史的列表locationBackList中。
                this.storageGroupExistInfo(this.j);

                this.findNodesOfSameGroupIdInGraph();
                // 用于判断是否已经存在该组，若已存在。则不再生成新group，避免使用addItem('group')报错。

                this.isNeedGroupGenerate(this.nodes);
            } else {
                // console.log("!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!")
                // 这里else就是已经用户开始点击来触发post请求，拿到的新数据。
                this.isLayoutByRowOrCol()
            }
            this.treeGraph.refresh()
        },
        postFromQuestion(answerId, caseId, submit) {
            this.axios
                .post(`/question`, {
                    ans_id: answerId,
                    case_id: caseId,
                    submit: submit
                })
                .then((res) => {
                    if (res.status === 200 && res.data.code === 1) {
                        this.originQuestionId = res.data.data.question[0]
                        this.originGroupId = res.data.data.question[0]
                        this.originQuestionLabel = res.data.data.question[1]
                        // console.log("this.originQuestionId:", this.originQuestionId, typeof (this.originQuestionId))
                        // console.log("this.originQuestionLabel:", this.originQuestionLabel)
                        this.originOptions = res.data.data.answers
                        // console.log(this.originOptions)
                        this.postFinishTimes++;
                    } else if (res.status === 200 && res.data.code === 0) {
                        this.$message({
                            message: "读取完成",
                            type: "warning",
                        });
                    } else {
                        // console.log("else");
                        this.$message({
                            message: res.data.msg,
                            type: "warning",
                        });
                    }
                })
                .catch((err) => {
                    // console.log(err);
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                })
        },
        changesToServe(changeList) {
            // changeList.forEach((item) => {
                this.axios
                    .post(`/changeAnswer`, changeList)
                    .then((res) => {
                        if (res.status === 200 && res.data.code === 0) {
                            // console.log("ok")
                            this.postFromServerClass_question = res.data.data
                            // console.log("this.postFromServerClass_question:", this.postFromServerClass_question)
                        } else {
                            // console.log("else");
                            this.$message({
                                message: res.data.data.msg,
                                type: "warning",
                            });
                        }
                    })
                    .catch((err) => {
                        // console.log(err);
                        this.$message({
                            message: "服务器异常",
                            type: "warning",
                        });
                    })

            // })
        },
        // 解决文字编辑换行问题。
        fittingString(str, fontTotal) {
            let res = '';
            let total = str.length;
            let start = 0;
            let cnt = 1;
            if (total < fontTotal) {
                res = str;
            } else {
                str.split('').forEach((letter, i) => {
                    if (fontTotal * cnt > total) return;
                    if (i >= fontTotal * cnt) {
                        res += `${str.substr(start, fontTotal)}\n`;
                        start = i;
                        cnt++;
                    }
                    if (fontTotal * cnt > total) {
                        res += `${str.substr(i)}`;
                    }
                });
            }
            return res;
        },
        // 点击“修改”按钮后触发的事件。
        showLableTextButtonClick() {
            this.newNode.label = this.fittingString(this.labelText, this.fontLimitNewNode);
            const item = this.treeGraph.findById(this.chooseNodeId);
            // console.log(item)
            this.treeGraph.updateItem(item, JSON.parse(JSON.stringify(this.newNode)));
            this.changeQuestionObj.current_id = this.chooseNodeId
            this.changeQuestionObj.operation = "change"
            this.changeQuestionObj.new_value = this.newNode.label
            this.changeQuestionObj.question_class = item._cfg.model.groupId
            this.changeToServeList.push(JSON.parse(JSON.stringify(this.changeQuestionObj)))
            // console.log("this.changeToServeList:", this.changeToServeList)
        },
        // 右上角创建问题节点按键的触发函数。
        CreateNewQuestion(value) {
            const CreatObj = [{
                current_id:'',
                operation:'create',
                new_value:value,
                question_class:'',
            }];
            this.axios
                    .post(`/changeAnswer`, CreatObj)
                    .then((res) => {
                        if (res.status === 200 && res.data.code === 0) {
                            this.postFromServerClass_question = res.data.data
                            console.log(this.postFromServerClass_question)
                            this.addNodeAuto(this.postFromServerClass_question, '', this.postFromServerClass_question,700,100)
                        } else {
                            this.$message({
                                message: res.data.data.msg,
                                type: "warning",
                            });
                        }
                    })
                    .catch((err) => {
                        this.$message({
                            message: "服务器异常",
                            type: "warning",
                        });
                    })
        },
        // 用于每次点击后自动增加节点。(此函数用户操作不感知)
        addNodeAuto(id, label, groupId, x, y) {
            this.treeGraph.addItem('node', {
                type: 'rect',
                size: [250, 40],
                id: id,
                // groupId: '000001',
                groupId: groupId,
                label: label,
                x: x,
                y: y,
            });
            // 当源数据中现有节点/边/ Combo 的数据项发生配置的变更时，根据新数据刷新视图。
            this.treeGraph.refresh()
        },
        addEdge(source, target) {
            this.treeGraph.addItem('edge', {
                source: source,
                target: target,
                type: 'line',
                // type: 'polyline',
                id: `edge${((this.edgeId)++).toString()}`,
            });
            this.treeGraph.refresh();
        },
        // 用户删除Node操作。(一删除node和edge的信息都没有了，取不出来数据。)重新改为右侧删除按钮。
        recursiveDeleteNode() {
            const item = this.treeGraph.findById(this.chooseNodeId)
            let OutEdge = item.getOutEdges()
            let InEdge = item.getInEdges()
            OutEdge.forEach((edge) => {
                this.treeGraph.removeItem(edge)
            })
            InEdge.forEach((edge) => {
                this.treeGraph.removeItem(edge)
            })
            // console.log(node)
            this.treeGraph.removeItem(item)

            this.changeQuestionObj.current_id = this.chooseNodeId
            this.changeQuestionObj.operation = "delete"
            this.changeQuestionObj.new_value = ''
            this.changeQuestionObj.question_class =''
            // this.changeQuestionObj.question_class = item._cfg.model.groupId
            this.changeToServeList.push(JSON.parse(JSON.stringify(this.changeQuestionObj)))

            // console.log(this.chooseNodeId)
            console.log("this.changeToServeList:", this.changeToServeList)
        },
        saveConfig() {
            this.changesToServe(this.changeToServeList);
        },
        initGraph() {
            //this代表上下文，在initGraph中this还是vue，但是在initGraph()里面的new G6.Menu()中this就取不到vue了，
            //所以要定义个变量vm来接收vue。
            let vm = this;
            // const minimap = new G6.Minimap({
            //     size: [150, 100],
            // })
            // 实例化Menu插件
            const menu = new G6.Menu({
                offsetX: 280,
                offsetY: 30,
                // menu作用在node类型上面。
                iterTypes: ['node'],
                getContent() {
                    const outDiv = document.createElement('div');
                    outDiv.style.width = "60px",
                        outDiv.innerHTML = `
                                        <p>新节点</p>
                                        <p>外联</p>`
                    return outDiv
                },
                //点击menu上的标签后的相应函数
                handleMenuClick(target) {
                    // console.log(target)
                    switch (target.innerHTML) {
                        case '新节点': {
                            vm.addNodeAuto();
                            break;
                        }
                        case '外联': {
                            // 从default模式设置到自定义的加边的addEdge模式上
                            vm.treeGraph.setMode("addEdge")
                            break;
                        }
                    }
                },
            });
            // 封装注册切换到点击添加边的函数
            // 在'click-add-edge'的行为里面注册了
            // 对应'node:click'事件的函数'onClick'
            // 对应mousemove事件的函数'onMousemove'
            // 'edge:click'事件的函数'onEdgeClick'
            // 'canvas:click'事件的函数'onCanvasClick'
            G6.registerBehavior('click-add-edge', {
                // 设定该自定义行为需要监听的事件及其响应函数
                getEvents() {
                    // 监听的事件为 node:click,响应函数是onClick
                    // 监听的事件为 mousemove,响应函数是onMousemove
                    // 监听的事件为 edge:click,响应函数是onEdgeClick
                    return {
                        'node:click': 'onClick',
                        mousemove: 'onMousemove',
                        'edge:click': 'onEdgeClick', // 点击空白处，取消边,并进入默认模式。
                    };
                },
                // 点击事件

                onClick(ev) {
                    // console.log('this:', this)
                    const node = ev.item;
                    const graph = this.graph;
                    // 这里的point是外联时，点击的第一个node时鼠标的坐标（x， y）。
                    const point = {
                        x: ev.x,
                        y: ev.y
                    };
                    const model = node.getModel();
                    if (this.addingEdge && this.edge) {
                        // console.log("this.addingEdge:", this.addingEdge);
                        // console.log("if_this.edge:", this.edge);
                        // 3.在连线过程中，点击第二个node时，updateItem新增加的边，将其target设置为点击的node。
                        // console.log(model.id)
                        // console.log(vm.treeGraph, graph)
                        graph.updateItem(this.edge, {
                            target: model.id
                        });
                        this.edge = null;
                        this.addingEdge = false;
                    } else {
                        // console.log("this.addingEdge:", this.addingEdge);
                        // console.log("this.edge:", this.edge);
                        // console.log(model.id)
                        // 1.首先点击node后，增加一条边，source为点击的node，target为鼠标点击时的坐标。
                        this.edge = graph.addItem('edge', {
                            source: model.id,
                            target: point,
                            type: 'polyline',
                            id: `edge${Math.random()}`,
                        });
                        // console.log("else_this.edge:", this.edge)
                        this.addingEdge = true;
                    }
                },
                onMousemove(ev) {
                    const point = {
                        x: ev.x,
                        y: ev.y
                    };
                    // Point为鼠标滑动时的坐标。(Point随鼠标一直在变化)。
                    // console.log("onMousemovethis.addingEdge", this.addingEdge)
                    // console.log("onMousemovethis.edge", this.edge)
                    if (this.addingEdge && this.edge) {
                        // 2.source为点击的node，target为鼠标滑动时的坐标。
                        this.graph.updateItem(this.edge, {
                            target: point
                        });
                        // console.log(point)
                    }
                },
                onEdgeClick(ev) {
                    const currentEdge = ev.item;
                    // 拖拽过程中，点击会点击到新增的边上
                    if (this.addingEdge && this.edge == currentEdge) {
                        // vm.treeGraph.removeItem(this.edge);
                        vm.treeGraph.removeItem(this.edge);
                        this.edge = null;
                        this.addingEdge = false;
                    }
                    vm.treeGraph.setMode("default")
                }
            });

            // 封装点击添加节点的交互
            G6.registerBehavior('onClickDefault', {
                // 设定该自定义行为需要监听的事件及其响应函数。
                getEvents() {
                    return {
                        'node:click': 'onClickDefaultInput'
                    };
                },
                // 点击事件vm
                onClickDefaultInput(ev) {
                    vm.isShowLabelText = true;
                    vm.labelText = ev.item._cfg.model.label
                    vm.newNode.id = ev.item._cfg.model.id
                    vm.newNode.x = ev.x
                    vm.newNode.y = ev.y
                    // 单击时，左边编辑框出现节点的ID和groupID
                    vm.chooseNodeId = ev.item._cfg.model.id
                    vm.chooseNodeGroupId = ev.item._cfg.model.groupId
                    // console.log("click groupId:", ev.item._cfg.model.groupId)
                }
            });
            G6.registerBehavior('onDblClick', {
                // 设定该自定义行为需要监听的事件及其响应函数。
                getEvents() {
                    return {
                        'node:dblclick': 'onDblClickEvent'
                    };
                },
                // 点击事件vm
                onDblClickEvent(ev) {
                    // console.log("dblClick ev.item._cfg.id:", ev.item._cfg.id, typeof (ev.item._cfg.id))
                    vm.postFromQuestion(ev.item._cfg.model.id, '', 0)
                    // console.log("dblClick originOptions:", vm.originOptions)
                    vm.chooseNodeId = ev.item._cfg.model.id
                    vm.treeGraph.refresh()

                }
            });

            // Init Graph
            this.treeGraph = new G6.Graph({
                container: 'mountNode',
                height: this.canvasHeight,
                width: this.canvasWidth,
                // plugins: [menu, minimap],
                plugins: [menu],

                // 交互模式集合
                modes: {
                    // 默认交互模式
                    default: [{},
                        'drag-canvas', //可拖动
                        'zoom-canvas', //可缩放
                        'drag-node',
                        'click-select',
                        'onClickDefault',
                        'onDblClick',
                        'drag-group',
                        'drag-node-with-group',
                    ],
                    // 增加边交互模式
                    addEdge: ['click-add-edge', 'click-select', 'onDblClick'],
                },
                layout: {
                    type: 'dagre',
                    // 布局的方向。LR：从左至右布局。
                    rankdir: 'LR',
                    // rankdir: 'TB',
                    // 节点对齐方式。DL：对齐到左下角。
                    align: 'DL',
                    preventOverlap: true,
                },
                defaultNode: {
                    type: 'rect',
                    size: [200, 70],
                    style: {
                        fill: 'white', //节点填充色
                        stroke: 'gray', //节点描边色,
                        lineWidth: 0.4, // 节点描边粗细
                        cursor: 'pointer'
                    },
                    labelCfg: {
                        autoRotate: true,
                        position: 'center',
                        // position为'center'时，offset无关。
                        // offset:1000,
                        style: {
                            fill: '#000', //节点标签文字颜色
                            fontSize: 12,
                        },
                    },
                    anchorPoints: [
                        [0, 0.5],
                        [1, 0.5],
                    ],
                },
                defaultEdge: {
                    style: {
                        stroke: '#000',
                        lineWidth: 1,
                        lineAppendWidth: 3,
                        cursor: 'crosshair',
                        endArrow: true,
                        sourceAnchor: 1,
                        targetAnchor: 1,
                    },
                },

                // The node styles in different states
                nodeStateStyles: {
                    // The node styles in selected state, corresponds to the built-in click-select behavior
                    selected: {
                        stroke: '#FAEBD7',
                        lineWidth: 0.7,
                        fill: '#F0F8FF'
                    },
                },
                edgeStateStyles: {
                    active: {
                        stroke: '#999'
                    }
                },
                groups: [],
                groupType: 'rect',
            });
        },
    }
}
</script>

<style>
.editLeftArea {
    width: 100%;
