<template>
<full-page ref="fullpage" :options="options" id="fullpage">
    <div class="page-header">
        <div class="w">
            <span class="page-header-logo">LOGO</span>
            <span class="page-header-nav" @click="jumpToHome">首页</span>
            <span class="page-header-nav" @click="jumpToPricloud">私有云</span>
            <span class="page-header-nav">案例</span>
            <span class="page-header-nav" @click="jumpToQA">个性化定制</span>
        </div>
    </div>
    <div class="section">
        <div class="page1" :style="{backgroundImage: 'url('+ page1Bg +')'}">
            <swiper ref="mySwiper" :options="swiperOptions">
                <swiper-slide v-for="(item,index) in firstPageWord" :key="index.toString()">
                    <div class="w">
                        <div class="page1-container" v-if="firstPageWord">
                            <div class="page1-container-words">
                                <transition name="title-slide-fade">
                                    <div v-show="showFirst" class="page1-title">{{item.title}}</div>
                                </transition>
                                <transition name="content-slide-fade">
                                    <div v-show="showFirst" class="page1-content">{{item.content}}</div>
                                </transition>
                            </div>
                            <img class="page1-container-module" :src="'/getFile?file_path=' + item.url" />
                        </div>
                    </div>
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div>
    <div class="section">
        <div class="page2" :style="{backgroundImage: 'url('+ page2Bg +')'}">
            <div class="w">
                <div class="page2-container">
                    <div v-show="priCloudInfo">
                        <div class="page2-title" v-if="secondPageWord[0]">{{secondPageWord[0].title}}</div>
                        <div class="page2-content" v-if="secondPageWord[0]">{{secondPageWord[0].content}}</div>
                    </div>
                    <div v-show="hyperFuseInfo">
                        <div class="page2-title" v-if="secondPageWord[1]">{{secondPageWord[1].title}}</div>
                        <div class="page2-content" v-if="secondPageWord[1]">{{secondPageWord[1].content}}</div>
                    </div>
                    <div class="page2-icon">
                        <div class="private-cloud-icon" :style="{backgroundImage: 'url('+ page2Icon1 +')'}" @mouseenter="enterPriCloud()" @mouseleave="leavePriCloud()"></div>
                        <div class="hyper-fuse-icon" :style="{backgroundImage: 'url('+ page2Icon2 +')'}" @mouseenter="enterHyperFuse()"></div>
                    </div>
                    <div class="page2-progress">
                        <div :class="['default-progress',priCloudInfo ? 'active-progress':'']"></div>
                        <div :class="['default-progress',hyperFuseInfo ? 'active-progress':'']"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="section">
        <div class="page3" :style="{backgroundImage: 'url('+ page3Bg +')'}">
            <div class="w">
                <div class="page3-container">
                    <swiper :options="thirdPageSwiperOption">
                        <swiper-slide class="third-page-slide" v-for="(item,index) in thirdPageSlide" :key="index.toString()">
                            <img class="third-page-img" :src="item.imgUrl" />
                        </swiper-slide>
                    </swiper>
                </div>
            </div>
        </div>
    </div>
    <div class="section">
        <div class="page4" :style="{backgroundImage: 'url('+ page4Bg +')'}">
            <div class="w">
                <div class="page4-container">
                    <div class="page4-container-left">
                        <div class="page4-phone">咨询电话: 029-881-8888</div>
                        <div class="page4-info">地址: 陕西省西安市西咸新区秦汉新城兰池大道</div>
                        <div class="page4-info">邮编: 066000</div>
                        <div class="page4-info">邮箱: ueucdud@vaaaa.com</div>
                        <div class="page4-info">技术支持: 奥卡云数据科技有限公司</div>
                    </div>
                    <div class="page4-container-right">
                        <div class="page4-right-content">
                            <span>首页</span>
                        </div>
                        <div class="page4-right-content">
                            <span @click="jumpToPricloud">私有云</span>
                        </div>
                        <div class="page4-right-content">
                            <span>案例</span>
                        </div>
                        <div class="page4-right-content">
                            <span @click="jumpToQA">个性化定制</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="section fp-auto-height">
        <div class="footer">
            <div class="footer-content">Copyright ©2017-2020 西安奥卡云数据科技有限公司 版权所有 | 陕公网安备61040402000225号 | 陕ICP备18011301号</div>
        </div>
    </div>
</full-page>
</template>

<script>
import {
    getHomeInfo
} from "../../api";
import {
    visit
} from "../../api";
export default {
    components: {},
    data() {
        var vm = this;
        return {
            options: {
                afterLoad: function (anchors, next) {
                    if (next.index === 1) {
                        vm.slideToPage2 = true;
                    } else if (next.index === 2) {
                        vm.slideToPage3 = true;
                    } else {
                        vm.slideToPage2 = false;
                        vm.slideToPage3 = false;
                    }
                },
            },
            showFirst: false,
            slideToPage2: false,
            slideToPage3: false,
            // page2私有云和超融合文字描述
            priCloudInfo: true,
            hyperFuseInfo: false,
            page1Bg: '',
            firstPageWord: [],
            page2Bg: '',
            page2Icon1: '',
            page2Icon2: '',
            defaultPage2Icon1: '',
            defaultPage2Icon2: '',
            activePage2Icon1: '',
            activePage2Icon2: '',
            secondPageWord: [],
            page3Bg: '',
            thirdPageSlide: [{
                    imgUrl: require("../../assets/images/test1.jpg"),
                },
                {
                    imgUrl: require("../../assets/images/test2.jpg"),
                },
                {
                    imgUrl: require("../../assets/images/test3.jpg"),
                },
                {
                    imgUrl: require("../../assets/images/test4.jpg"),
                },
                {
                    imgUrl: require("../../assets/images/test5.jpeg"),
                },
                {
                    imgUrl: require("../../assets/images/test6.jpg"),
                },
            ],
            page4Bg: '',
            swiperOptions: {
                pagination: ".swiper-pagination",
                paginationType: "bullets",
                paginationClickable: true,
                loop: true,
                initialSlide: 1,
                // loopAdditionalSlides: 2,
                // observer: true,
                // observerParents: true,
                // autoplay: 4000,
                speed: 1000,
                slidesOffsetBefore: 800,
                autoplayDisableOnInteraction: false,
                onSlideChangeStart: function () {
                    vm.showFirst = false;
                },
                onTransitionEnd: function () {
                    vm.showFirst = true;
                },
            },
            thirdPageSwiperOption: {
                effect: "coverflow",
                // slidesPerView: 'auto',
                slidesPerView: 3,
                centeredSlides: false,
                slidesOffsetBefore: 300,
                // spaceBetween: 15,
                coverflow: {
                    rotate: 10,
                    stretch: 10,
                    depth: 200,
                    modifier: 2,
                    slideShadows: true,
                },
                loop: true,
                loopAdditionalSlides: 1,
                autoplay: 200,
                autoplayStopOnLast: false,
                speed: 1000,
                autoplayDisableOnInteraction: false,
            },
            // modulestyle: {
            //     left: '40%',
            //     width: '70%'
            // }
        };
    },
    mounted() {
        this.$nextTick(() => {

            this.showFirst = true;
            this.getHomeSection1Info()
            this.getHomeSection2Info()
            this.getHomeBg()
            this.active()
        })
    },
    updated() {
    
        console.log(document.getElementsByClassName('page1-container-module'))
    },
    computed: {},
    methods: {
        active() {
            visit({
                params: {
                    "flag": "0"
                },
                success: res => {
                    console.log("访问成功", res)
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        getHomeBg() {
            getHomeInfo({
                params: ['home1bg', 'home2bg', 'home3bg', 'home4bg'],
                success: (res) => {
                    if (res.data) {
                        this.page1Bg = (`/getFile?file_path=${res.data.home1bg.url}`)
                        this.page2Bg = (`/getFile?file_path=${res.data.home2bg.url}`)
                        this.page3Bg = (`/getFile?file_path=${res.data.home3bg.url}`)
                        this.page4Bg = (`/getFile?file_path=${res.data.home4bg.url}`)
                    } else {
                        console.log("数据获取失败")
                    }
                },
                fail: (res) => {
                    this.$message({
                        message: res.data.msg,
                        type: "warning",
                    });
                },
                error: (err) => {
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                },
            });
        },
        getHomeSection1Info() {
            getHomeInfo({
                params: ["home1slide1", "home1slide2", "home1slide3"],
                success: (res) => {
                    if (res.data) {
                        this.firstPageWord[0] = res.data.home1slide1
                        this.firstPageWord[1] = res.data.home1slide2
                        this.firstPageWord[2] = res.data.home1slide3
                    } else {
                        console.log("数据获取失败")
                    }
                },
                fail: (res) => {
                    this.$message({
                        message: res.data.msg,
                        type: "warning",
                    });
                },
                error: (err) => {
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                },
            });
        },
        getHomeSection2Info() {
            getHomeInfo({
                params: ["home2leftDefault", "home2rightDefault", "home2leftActiveImg", "home2rightActiveImg"],
                success: (res) => {
                    if (res.data) {
                        this.page2Icon1 = (`/getFile?file_path=${res.data.home2leftDefault.url}`)
                        this.page2Icon2 = (`/getFile?file_path=${res.data.home2rightDefault.url}`)
                        this.defaultPage2Icon1 = (`/getFile?file_path=${res.data.home2leftDefault.url}`)
                        this.defaultPage2Icon2 = (`/getFile?file_path=${res.data.home2rightDefault.url}`)
                        this.activePage2Icon1 = (`/getFile?file_path=${res.data.home2leftActiveImg.url}`)
                        this.activePage2Icon2 = (`/getFile?file_path=${res.data.home2rightActiveImg.url}`)
                        this.secondPageWord[0] = res.data.home2leftDefault
                        this.secondPageWord[1] = res.data.home2rightDefault
                    } else {
                        console.log("数据获取失败")
                    }
                },
                fail: (res) => {
                    this.$message({
                        message: res.data.msg,
                        type: "warning",
                    });
                },
                error: (err) => {
                    this.$message({
                        message: "服务器异常",
                        type: "warning",
                    });
                },
            });
        },

        handleSwiperReadied(swiper) {
            console.log("Swiper was munted!", swiper);
        },
        handleClickSlide(index, reallyIndex) {
            console.log("Click slide!", index, reallyIndex);
        },
        enterPriCloud() {
            this.priCloudInfo = true;
            this.hyperFuseInfo = false;
            this.page2Icon1 = this.activePage2Icon1
            this.page2Icon2 = this.defaultPage2Icon2
            this.$forceUpdate()
        },
        enterHyperFuse() {
            this.hyperFuseInfo = true;
            this.priCloudInfo = false;
            this.$nextTick(() => {
                this.page2Icon1 = this.defaultPage2Icon1
                this.page2Icon2 = this.activePage2Icon2
            })
        },
        jumpToQA() {
            this.$router.push({
                name: "qa",
            });
        },
        jumpToHome() {
            this.$router.push({
                name: "home",
            });
        },
        jumpToPricloud() {
            this.$router.push({
                name: "pricloud",
            });
        },
    },
};
</script>

<style scoped>


/* 导航栏 */
.page-header {
    width: 100vw;
    height: 56px;
    position: fixed;
    background-color: rgba(42, 48, 59, 0.5);
    z-index: 1000;
    color: #fff;
    line-height: 56px;
}

.page-header-logo {
    margin-right: 80px;
    font-size: 24px;
}

.page-header-nav {
    margin-right: 32px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}

/* 动画 */
.title-slide-fade-enter-active {
    transition: all 1.5s ease-out;
}

.content-slide-fade-enter-active {
    transition: all 1.5s ease-out 0.5s;
}

.title-slide-fade-enter {
    transform: translateY(20px);
    opacity: 0;
}

.content-slide-fade-enter,
.content-slide-fade-leave-to .content-slide-fade-leave-active {
    transform: translateY(20px);
    opacity: 0;
}

.page1 {
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
    padding-top: 56 px;
    position: relative;
}

.page1-container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 56px;
}

.page1-container-words {
    color: #fff;
    padding-top: 10%;
    position: relative;
    left: -60%;
    height: 100%;
    border: 0 none !important;
}

.page1-title {
    color: #fff;
    font-size: 48px;
    margin-bottom: 16px;
}

.page1-content {
    color: #fff;
    font-size: 24px;
    font-weight: 300;
}

.page1-container-module {
    position: relative;
}

@media screen and (max-width: 1420px) {
    .page1-container-module{
        left: 40%;
        width:70%;
    }
}

@media screen and (min-width: 1420px) {
    .page1-container-module{
        left: -800px;
        width:80%;
        height: 90%;
    }
}
.page2 {
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
    position: relative;
}

.page2-container {
    padding-top: 8%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.page2-title {
    font-size: 40px;
    margin-bottom: 5%;
    animation: myFade 2s;
    animation-timing-function: ease-out;
}

@keyframes myFade {
    0% {
        opacity: 0;
    }

    5% {
        opacity: 0.1;
    }

    100% {
        opacity: 1;
    }
}

.page2-content {
    font-size: 20px;
    line-height: 30px;
    height: 30vh;
    animation: myFade 2s;
    animation-timing-function: ease-out;
}

.page2-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 70%;
}

.private-cloud-icon {
    width: 120px;
    height: 120px;
    cursor: pointer;
}

.private-cloud-icon:hover {
    background-image: url("../../assets/images/activePriCloud.png");
    width: 120px;
    height: 120px;
}

.private-cloud-icon:hover .left-progress {
    background-color: #2e3969;
}

.hyper-fuse-icon {
    width: 120px;
    height: 120px;
    cursor: pointer;
}

.hyper-fuse-icon:hover {
    background-image: url("../../assets/images/activeHypFuse.png");
    width: 120px;
    height: 120px;
    cursor: pointer;
}

.page2-progress {
    width: 80%;
    height: 3px;
    margin-bottom: 3%;
    background-color: #afb6ce;
    display: flex;
    flex-direction: row;
}

.default-progress {
    width: 50%;
    height: 100%;
    background-color: #afb6ce;
}

.active-progress {
    width: 50%;
    height: 100%;
    background-color: #0172f5;
}

.page3 {
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
    padding-top: 56px;
    position: relative;
}

.page3-container {
    height: 100vh;
    width: 100%;
}
@media screen and (max-width: 1420px) {
    .page3-container{
    margin-top: 60px;
    }
}

@media screen and (min-width: 1420px) {
    .page3-container{
    margin-top: 200px;
    }
}
.third-page-img {
    height: 400px;
    width: 600px;
    transform: rotateY(-15deg);
}

.page4 {
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
    padding-top: 56px;
    position: relative;
}

.page4-container {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

@media screen and (max-width: 1420px) {
    .page4-container{
    margin-top: 120px;
    }
}

@media screen and (min-width: 1420px) {
    .page4-container{
    margin-top: 220px;
    }
}
.page4-container-left {
    color: #fff;
}

.page4-phone {
    font-size: 22px;
    margin-bottom: 48px;
}

.page4-info {
    margin-bottom: 37px;
}

.footer {
    background-color: #171b26;
    height: 10%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.page4-container-right {
    font-size: 16px;
    margin-left: 583px;
    color: #2e3969;
}

.page4-right-content {
    margin-bottom: 48px;
}

.page4-right-content>span {
    cursor: pointer;
}

.footer-content {
    color: #fff;
}

.fp-auto-height {
    height: 10% !important;
}

.fp-auto-height>.fp-tableCell {
    height: 10% !important;
}
</style>
