                        “云”的通用功能是提供某种IT服务，其服务仍然以计算、存储、网络等资源中的某个或某些元素为载体，并可通过量化指标测量其服务质量。这些元素在不同层面的组合即是不同种类的服务，比如IaaS层面提供虚拟机运行环境，PaaS层面提供应用运行环境，SaaS层面则直接提供终端应用。终端用户与服务提供商对“云”的理解有所不同——终端用户认为它是无所不在、随用随取、具有一定安全性和可靠性的服务实体，而服务提供商则将其定义为有弹性、可扩展的、前景广阔的IT基础架构。但不论从哪个角度考虑我们都会有一点共识，那就是“云即服务”。
