<template>
<div class="console-page">
    <div class="mask" v-show="reportShow"></div>
    <div class="console-page-top">
        <div class="basic-chart">
            <v-chart :options="clickPie" autoresize />
        </div>
        <div class="basic-chart">
            <v-chart :options="visitVolume" autoresize />
        </div>
        <div class="basic-chart" style="margin-right: 0px;">
            <v-chart :options="customQuantity" autoresize />
        </div>
    </div>
    <div class="console-page-bottom">
        <div class="table-title">
            <div class="table-title-left">
                <img :src="tabletitleIcon" />
                <span>定制申请列表</span>
            </div>
            <div class="search-input-view">
                <input placeholder="请输入手机号查询" v-model="keywords" @keyup.enter="searchInfo">
                <i class="iconfont iconchazhao" @click="searchInfo" v-if="hasResult"></i>
                <i class="iconfont iconguanbi" @click="cancelSearch" v-if="!hasResult"></i>
            </div>
        </div>
        <el-table class="eltable" :data="tableData" stripe width="400">
            <el-table-column type="index" label="序号" align="center"></el-table-column>
            <el-table-column prop="phone_number" label="手机号" align="center"></el-table-column>
            <el-table-column prop="industry" label="所属行业" align="center"></el-table-column>
            <el-table-column prop="scale" label="规模" align="center"></el-table-column>
            <el-table-column prop="server" label="服务器数量" align="center"></el-table-column>
            <el-table-column prop="time" label="递交时间" align="center"></el-table-column>
            <el-table-column label="报告" align="center">
                <template slot-scope="scope">
                    <i class="iconfont iconbaogao" @click="getReport(scope)"></i>
                </template>
            </el-table-column>
            <el-table-column prop="status" label="操作" align="center">
                <template slot-scope="scope">
                    <div style="color:#3DA58F" v-if="scope.row.status === 1">已处理</div>
                    <div style="color:red;cursor:pointer" v-if="scope.row.status === 0" @click="deal(scope)">未处理</div>
                </template>
            </el-table-column>
        </el-table>
    </div>
    <div class="console-page-footer">
        <div class="console-page-footer-left">
            <i class="iconfont icontongji4"></i>
            <span>共{{sumNum}}条信息</span>
        </div>
        <div class="console-page-footer-right">
            <el-pagination @current-change="handleCurrentChange" current-page.sync="1" :page-size="10" layout="prev, pager, next, jumper" :total="parseInt(sumNum)">
            </el-pagination>
        </div>
    </div>
    <div class="report-show" v-show="reportShow" id="report">
        <div class="report-view-console" ref="downloadreport">
            <div class="report-view-title">私有云部署方案</div>
            <div class="report-page-container">
                <div class="spliter"></div>
                <div class="private-cloud-section-title">私有云业务</div>
                <div class="private-cloud-business">
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">OA办公系统</div>
                    </div>
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">虚拟桌面</div>
                    </div>
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">OERP企业咨询计划</div>
                    </div>
                </div>
                <div class="spliter"></div>
                <div class="private-cloud-section-title">私有云硬件配置情况</div>
                <table class="report-table-view" cellpadding="10">
                    <tr bgcolor="#F1F3F9" width="300px">
                        <td>CPU型号：{{CPUType}}</td>
                        <td>CPU数量：{{CPUNum}}</td>
                    </tr>
                    <tr width="300px">
                        <td>内存规格：{{RAMType}}</td>
                        <td>内存数量：{{RAMNum}} </td>
                    </tr>
                    <tr bgcolor="#F1F3F9" width="300px">
                        <td>存储类型：{{storageType}}</td>
                        <td>存储容量：{{storageCapacity}}</td>
                    </tr>
                    <tr width="300px">
                        <td>网络类型：{{netType}} </td>
                        <td>网卡数量：{{netNum}}</td>
                    </tr>
                </table>
                <div class="private-cloud-section-title">企业IT 三年规划</div>
                <div class="company-plan">
                    <div class="plan-view">
                        <div class="plan-view-title">扩容方案</div>
                        <div class="plan-view-content">根据数据年增长率20%计算， 需新增节点 *， 即超融合服务器*台， 耗电量增加*W， 新增UPS设备 * 台。 </div>
                    </div>
                    <div class="plan-view">
                        <div class="plan-view-title">网络方案</div>
                        <div class="plan-view-content">根据数据年增长率20%计算，预计带宽为 *M, 现有带宽满足需求。 </div>
                    </div>
                </div>
            </div>
        </div>
        <i class="iconfont iconxiazai" @click="download()" style="font-size:25px;cursor:pointer;margin-left:100%"></i>
        <i class="iconfont iconguanbi" @click="reportHide()" style="font-size:25px;cursor:pointer;margin-left:100%" />
    </div>

</div>
</template>

<script>
import ECharts from 'vue-echarts'
import echarts from 'echarts'
import {
    getTableInfo
} from '../../api'
import {
    sumVisit
} from '../../api'
import {
    adminGetReport
} from '../../api'
import {
    adminDeal
} from '../../api'
import {
    lineChart1
} from '../../api'
export default {
    components: {
        'v-chart': ECharts
    },
    mounted() {
        this.$nextTick(() => {
            this.getTableData()
            this.getPieData()
            this.getLineData(0, 0, 'visitVolume')
            this.getLineData(1, 1, 'visitVolume')
            this.getLineData(2, 2, 'visitVolume')
            this.getLineData(3, 0, 'customQuantity')
        })
    },
    methods: {
        getReport(scope) {
            adminGetReport({
                params: {
                    case_id: scope.row.case_id
                },
                success: res => {
                    this.CPUType = res.data.cpu_type
                    this.CPUNum = res.data.cpu_count
                    this.RAMType = res.data.memory_type
                    this.RAMNum = res.data.memory_count
                    this.storageType = res.data.storage_type
                    this.storageCapacity = res.data.storage_count
                    this.netType = res.data.network_type
                    this.netNum = res.data.network_count
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
            this.reportShow = true
        },
        download() {
            // this.timeoutTimer = setTimeout(() => {
            //     this.$PDFSave(this.$refs.downloadreport, "客户报告");
            //     clearTimeout(this.timeoutTimer)
            // }, 1000)
            //用延时器没用，下载后的报告依然有阴影
            this.$PDFSave(this.$refs.downloadreport, "客户报告");
        },
        reportHide() {
            this.reportShow = false
        },
        handleCurrentChange(page) {
            this.pageNum = page
            this.getTableData()
        },
        getPieData() {
            sumVisit({
                success: res => {
                    this.$set(this.clickPie.series[0].data[0], 'value', res.data.custom)
                    this.$set(this.clickPie.series[0].data[2], 'value', res.data.homepage)
                    this.$set(this.clickPie.series[0].data[1], 'value', res.data.introduction)
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        getLineData(flag, index, objName) {
            lineChart1({
                params: {
                    type: flag,
                    num: 4,
                },
                success: res => {
                    let xAxisData = [];
                    let seriesData = [];
                    res.data.map(item => {
                        xAxisData.push({
                            value: item.date,
                        })
                        seriesData.push(item.value || 0)
                    })
                    this.$set(this[objName].xAxis[0], 'data', xAxisData)
                    this.$set(this[objName].series[index], 'data', seriesData)
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        getTableData() {
            getTableInfo({
                params: {
                    "page_num": parseInt(this.pageNum),
                    "page_size": parseInt(this.pageSize),
                    "query": this.keywords
                },
                success: res => {
                    this.tableData = res.data.case
                    this.sumNum = res.data.sum_cases
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            })
        },
        searchInfo() {
            getTableInfo({
                params: {
                    "page_num": parseInt(this.pageNum),
                    "page_size": parseInt(this.pageSize),
                    "query": this.keywords
                },
                success: res => {
                    this.tableData = res.data.case
                    this.sumNum = res.data.sum_cases
                    this.hasResult = false
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            })
        },
        cancelSearch() {
            this.keywords = ''
            this.hasResult = true
            this.getTableData()
        },
        deal(scope) {
            adminDeal({
                params: {
                    case_id: scope.row.case_id
                },
                success: res => {
                    scope.row.status = 1
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            })
        }
    },
    data() {
        return {
            tabletitleIcon: require("../../assets/images/backcontrol/组 140.png"),
            bussinessUrl: require('../../assets/images/reportView.png'),
            pageNum: '1',
            pageSize: '10',
            keywords: '',
            sumNum: '',
            hasResult: 'ture',
            reportShow: false,
            CPUType: '',
            CPUNum: '',
            RAMType: '',
            RAMNum: '',
            storageType: '',
            storageCapacity: '',
            netType: '',
            netNum: '',
            clickPie: {
                title: {
                    text: '页面点击统计',
                    left: '30',
                    top: '20'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: '70%',
                    top: 'center',
                    data: ['定制申请页', '主页', '私有云介绍页', ]
                },
                color: ['#86b6f2', '#ffb359', '#55dbe3'],
                series: [{
                        name: ['访问量'],
                        type: 'pie',
                        radius: [20, 110],
                        center: ['35%', '50%'],
                        roseType: 'radius',
                        label: {
                            show: false
                        },
                        emphasis: {
                            label: {
                                show: true
                            }
                        },
                        data: [{
                                value: '',
                                name: '定制申请页'
                            },
                            {
                                value: '',
                                name: '私有云介绍页'
                            },
                            {
                                value: '',
                                name: '主页'
                            }
                        ]
                    },

                ]
            },
            visitVolume: {
                title: {
                    text: '访问量统计',
                    textStyle: {
                        color: '#4F4E4E',
                        letterSpace: 1,
                    },
                    top: '5%',
                    left: '5%'
                },
                tooltip: {
                    trigger: 'axis',
                    // 坐标轴显示器
                    axisPointer: {
                        show: true,
                        type: 'cross',
                        label: {
                            backgroundColor: '#6a7985'
                        }
                    }
                },
                grid: {

                },
                xAxis: [{
                    type: 'category',
                    boundaryGap: false,
                    data: [],
                    axisLable: {
                        show: true,
                    },
                    axisTick: {
                        show: false //不显示坐标轴刻度线
                    },
                    axisLine: {
                        show: false, //不显示坐标轴线
                    },
                }],
                yAxis: [{
                    type: 'value',
                    axisTick: {
                        show: false //不显示坐标轴刻度线
                    },
                    axisLine: {
                        show: false, //不显示坐标轴线
                    },
                    axisLable: {
                        show: false,
                        rotate: '45',
                        fontSize: 15,
                        textStyle: {
                            color: '#000',
                            fontSize: 15
                        }
                    },
                }],
                series: [{
                    name: '主页',
                    type: 'line',
                    stack: '主页',
                    smooth: true, // 平滑曲线
                    lineStyle: {
                        color: '#55dbe3'
                    },
                    itemStyle: {
                        color: '#55dbe3'
                    },
                    label: {
                        fontSize: 25,
                        normal: {
                            show: true,
                            position: 'top',
                            fontSize: 15,
                        }
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(85, 219, 227, 0.8)'
                            }, {
                                offset: .4,
                                color: 'rgba(85, 219, 227, 0.3)'
                            }, {
                                offset: 1,
                                color: 'rgba(85, 219, 227, 0.00)'
                            }])
                        }
                    },
                    data: []
                }, {
                    name: '私有云',
                    type: 'line',
                    stack: '私有云',
                    smooth: true, // 平滑曲线
                    lineStyle: {
                        color: 'rgba(85, 19, 227, 1)'
                    },
                    itemStyle: {
                        color: 'rgba(85, 19, 227, 1)'
                    },
                    label: {
                        fontSize: 25,
                        normal: {
                            show: true,
                            position: 'top',
                            fontSize: 15,
                        }
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(85, 19, 227, 0.8)'
                            }, {
                                offset: .4,
                                color: 'rgba(85, 19, 227, 0.3)'
                            }, {
                                offset: 1,
                                color: 'rgba(85, 19, 227, 0.00)'
                            }])
                        }
                    },
                    data: []
                }, {
                    name: '私人定制',
                    type: 'line',
                    stack: '私人定制',
                    smooth: true, // 平滑曲线
                    lineStyle: {
                        color: 'rgba(85, 119, 227, 1)'
                    },
                    itemStyle: {
                        color: 'rgba(85, 119, 227, 1)'
                    },
                    label: {
                        fontSize: 25,
                        normal: {
                            show: true,
                            position: 'top',
                            fontSize: 15,
                        }
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(85, 119, 227, 0.8)'
                            }, {
                                offset: .4,
                                color: 'rgba(85, 119, 227, 0.3)'
                            }, {
                                offset: 1,
                                color: 'rgba(85, 119, 227, 0.00)'
                            }])
                        }
                    },
                    data: []
                }]
            },
            customQuantity: {
                title: {
                    text: '定制量统计',
                    textStyle: {
                        color: '#4F4E4E',
                        letterSpace: 1,
                    },
                    top: '5%',
                    left: '5%'
                },
                tooltip: {
                    trigger: 'axis',
                    // 坐标轴显示器
                    axisPointer: {
                        show: true,
                        type: 'cross',
                        label: {
                            backgroundColor: '#6a7985'
                        }
                    }
                },

                grid: {

                },
                xAxis: [{
                    type: 'category',
                    boundaryGap: false,
                    data: [],
                    axisLable: {
                        show: true,
                    },
                    axisTick: {
                        show: false //不显示坐标轴刻度线
                    },
                    axisLine: {
                        show: false, //不显示坐标轴线
                    },
                }],
                yAxis: [{
                    type: 'value',
                    axisTick: {
                        show: false //不显示坐标轴刻度线
                    },
                    axisLine: {
                        show: false, //不显示坐标轴线
                    },
                    axisLable: {
                        show: false,
                        rotate: '45',
                        fontSize: 15,
                        textStyle: {
                            color: '#000',
                            fontSize: 15
                        }
                    },
                }],
                series: [{
                    name: '定制量',
                    type: 'line',
                    stack: '总量',
                    smooth: true, // 平滑曲线
                    lineStyle: {
                        color: '#86b6f2'
                    },
                    itemStyle: {
                        color: '#86b6f2'
                    },
                    label: {
                        fontSize: 25,
                        normal: {
                            show: true,
                            position: 'top',
                            fontSize: 15,
                        }
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(134, 182, 242, 0.8)'
                            }, {
                                offset: .4,
                                color: 'rgba(134, 182, 242, 0.3)'
                            }, {
                                offset: 1,
                                color: 'rgba(134, 182, 242, 0)'
                            }])
                        }
                    },
                    data: []
                }]
            },
            tableData: [
                // {
                //         index: 1,
                //         phone_number: 12222222222,
                //         industry: 'ssdds',
                //         scale: '100-200',
                //         server: '30',
                //         time: '2020-8-3',
                //         status: 0,
                //         case_id: '111'
                //     }
            ]
        }
    }
}
</script>

<style scoped>
.echarts {
    width: 100%;
    height: 100%;
}

.console-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    /* margin-top: 10px; */
    padding: 0 15px;
    overflow-x: auto;
}

.mask {
    height: 100%;
    width: 100%;
    background-color: #000;
    opacity: 0.5;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.console-page-top {
    width: 100%;
    height: 350px;
    display: flex;
    flex-direction: row;
    margin-top: 15px;
}

.basic-chart {
    height: 100%;
    width: calc((100%-100px)/3);
    background-color: #fff;
    margin-right: 10px;
    border-radius: 6px;
}

.console-page-bottom {
    height: auto;
    margin-top: 15px;
}

.table-title {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    margin-bottom: 2px;
    padding: 10px 20px;
}

.table-title-left>span {
    font-size: 18px;
    padding-left: 10px;
    line-height: 37px;
}

.search-input-view {
    width: 200px;
    margin-top: 5px;
    text-align: center;
}

.search-input-view>input {
    height: 30px;
}

.search-input-view>input:focus {
    outline: none;
}

.search-input-view>i {
    padding-left: 5px;
    cursor: pointer;
}

.console-page-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 2%;
}

.console-page-footer-left {
    margin-left: 10px;
}

.console-page-footer-right {
    padding-right: 10px;
}

.eltable {
    border-radius: 5px;
}

.iconbaogao {
    cursor: pointer;
}

.iconchazhao {
    cursor: pointer;
}

.report-show {
    position: fixed;
    z-index: 5000;
}

@media screen and (max-width: 1420px) {
    .report-show {
        left: 200px;
        width: 1000px;
        height: 650px;
        top: 0;
    }
}

@media screen and (min-width: 1420px) {
    .report-show {
        left: 30%;
        width: 830px;
        height: 650px;
        top: 13%;
    }
}

.report-view-console {
    background-color: #fff;
    width: 100%;
    height: 100%;
}

.iconguanbi {
    font-size: 25px;
    cursor: pointer;
    position: absolute;
    top: 15px;
    left: -45px;
}

.iconxiazai {
    font-size: 25px;
    cursor: pointer;
    position: absolute;
    top: 15px;
    left: -85px;
}
</style>
