import Service from './request'
function _fetch(config, data) {
    return Service({
        url: config.url,
        method: config.method,
        headers: config.headers,
        data: data.params
    }).then(res => {
        if (res.code == 0) {
            data.success && typeof data.success === 'function' && data.success(res)
        } else {
            data.fail && typeof data.fail === 'function' && data.fail(res)
        }
    }).catch(err => {
        throw err
        data.error && typeof data.error === 'function' && data.error(err)
    })
}

export const getHomeInfo = function (data) {
    let config = {
        url: `/indexImage`,
        method: 'post',
    }
    return _fetch(config, data)
}
