<template>
<div class="edithome-page">
    <div class="edithome-page-header">
        <img :src="titleIcon" />
        <span>定制申请列表</span>
    </div>
    <div class="edithome-page-container">
        <div class="select-flag">
            <div class="select-flag-tips"><span style="color:red;padding:0 5px;">*</span>请选择需要更改的页面：</div>
            <input type="text" placeholder="flag" v-model="flag">
        </div>
        <div class="edit-title">
            <div class="edit-title-tips">题目编辑:</div>
            <input type="text" placeholder="title" v-model="title">
        </div>
        <div class="edit-content">
            <div class="edit-content-tips">文字编辑（300字以内）：</div>
            <textarea class="edit-content-box" v-model="content" maxlength="300" onchange="this.value=this.value.substring(0,300)" onkeydown="this.value=this.value.substring(0,300)" onkeyup="this.value=this.value.substring(0,300)" @input="descInput"></textarea>
            <span><span id="conterNum">{{conterNum}}</span>/300</span>
            <div class="edit-content-submit-btn" @click="submitContent">保存</div>
        </div>
        <div class="edit-img">
            <div class="edit-img-tips"><span style="color:red;padding:0 5px;">*</span>选择更改图片：</div>
            <div class="upload-view">
                <vue-dropzone v-if="dropzoneOptions.url" class="upload-img" ref="myVueDropzone" id="dropzone" :options="dropzoneOptions"></vue-dropzone>
                <span class="upload-tips" v-else>请编辑文字信息后上传图片</span>
            </div>
        </div>
        <div class="reset-edit" @click="resetEdit">重置</div>
    </div>
</div>
</template>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import {
    editHomeInfo
} from '../../api'
export default {
    name: 'app',
    components: {
        vueDropzone: vue2Dropzone
    },
    methods: {
        descInput() {
            this.conterNum = this.content.length;
        },
        submitContent() {
            console.log(this.content)
            if (this.flag) {
                this.$message({
                    message: '信息已上传，请稍后上传图片',
                    type: 'success'
                })
                editHomeInfo({
                    params: {
                        "content": this.content,
                        "flag": this.flag,
                        "title": this.title,
                    },
                    success: res => {
                        this.case_id = res.data
                        this.$set(this.dropzoneOptions, 'url', `/uploads/${res.data}`)
                        // console.log(res.data)
                        // console.log(this.dropzoneOptions.url)
                    },
                    fail: res => {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    },
                    error: err => {
                        this.$message({
                            message: "服务器异常",
                            type: 'warning'
                        })
                    }
                })
            } else {
                this.$message({
                    message: "请选择更改的页面",
                    type: 'warning'
                })
            }

        },
        resetEdit() {
            this.flag = '',
                this.title = '',
                this.content = '',
                this.conterNum = 0,
                this.dropzoneOptions.url = ''
        }

    },
    data() {
        return {
            titleIcon: require("../../assets/images/backcontrol/组 140.png"),
            flag: '',
            img: '',
            title: '',
            content: '',
            case_id: '',
            conterNum: 0,
            dropzoneOptions: {
                url: '',
                thumbnailWidth: 100,
                thumbnailHeight: 100,
                headers: {
                    "My-Awesome-Header": "header value"
                },
                acceptedFiles: '.png,.jpg',
                reset: function () {

                }
            },
        }
    }
}
</script>

<style>
input:focus {
    outline: none;
}
textarea:focus {
    outline: none;
}
.edithome-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    /* background-color: #666; */
    margin: 10px 15px;
}

.edithome-page-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    background-color: #fff;
    margin-bottom: 2px;
    padding: 10px 20px;
    height: 52px;
}

.edithome-page-header>span {
    padding-top: 5px;
    padding-left: 10px;
    font-size: 18px;
}

.edithome-page-container {
    height: auto;
    background-color: #fff;
    display: flex;
    flex-direction: column;
}

.select-flag {
    height: 100px;
    padding-left: 20px;
}

.select-flag>input {
    height: 30px;
    margin-left: 50px;
}

.select-flag-tips {
    height: 50px;
    line-height: 50px;
}

.select-flag-submit-btn {
    margin-left: 30px;
    padding: 5px 15px;
    background-color: mediumslateblue;
    color: #fff;
    cursor: pointer;
}

.edit-title {
    height: 100px;
    padding-left: 20px;
}

.edit-title>input {
    height: 30px;
    margin-left: 50px;
}

.edit-title-tips {
    height: 50px;
    line-height: 50px;
}

.edit-title-submit-btn {
    margin-left: 30px;
    padding: 5px 15px;
    background-color: mediumslateblue;
    color: #fff;
    cursor: pointer;
}

.edit-content {
    height: auto;
    padding-left: 20px;
}

.edit-content-tips {
    height: 50px;
    line-height: 50px;
}

.edit-content-box {
    width: 50%;
    height: 150px;
    margin-left: 50px;
    /* border: 1px solid #000; */
}

.edit-content-submit-btn {
    width: 62px;
    margin-top: 30px;
    margin-left: 55%;
    padding: 5px 15px;
    background-color: mediumslateblue;
    color: #fff;
    cursor: pointer;
}

.edit-img {
    height: auto;
    padding-left: 20px;
    width: 100%;
}

.edit-img-tips {
    height: 50px;
    line-height: 50px;
}

.upload-view {
    width: 50%;
    height: 200px;
    margin-left: 50px;
    border: 1px solid #a5a5a5;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.upload-img {
    width: 100%;
    height: 100%;
}

.edit-img-submit-btn {
    height: 31px;
    margin-top: 20px;
    margin-left: 50%;
    padding: 5px 15px;
    background-color: mediumslateblue;
    color: #fff;
    cursor: pointer;
}

.reset-edit {
    height: 31px;
    width: 62px;
    margin-top: 20px;
    margin-left: 2%;
    padding: 5px 15px;
    background-color: #123456;
    color: #fff;
    cursor: pointer;
    margin-bottom: 20px;
}

</style>
