import Home from '../pages/home'
import Report from '../pages/report'
import Question_Answer from '../pages/Q&A'
import Pricloud from '../pages/pricloud'
import Backcontrol from '../pages/backcontrol'
import Console from '../pages/console'
import editQuestion from '../pages/editQuestion'
import EditHome from '../pages/editHome'
import AdminLogin from '../pages/adminLogin'


export default [{
    path: '/',
    name: '',
    redirect: '/home'
}, {
    path: '/home',
    name: 'home',
    component: Home,
}, {
    path: '/report',
    name: 'report',
    component: Report,
}, {
    path: '/pricloud',
    name: 'pricloud',
    component: Pricloud,
}, {
    path: '/QA',
    name: 'qa',
    component: Question_Answer,
},
{
    path: '/backcontrol/:id',
    name: 'backcontrol',
    component: Backcontrol,
    children: [{
        path: '/backcontrol/console',
        name: 'console',
        component: Console
    },
    {
        path: '/backcontrol/editQuestion',
        name: 'editQuestion',
        component: editQuestion
    },{
        path: '/backcontrol/editHome',
        name: 'editHome',
        component: EditHome
    }]
    },
    {
        path:'/adminLogin',
        name:'adminLogin',
        component:AdminLogin,
    
    }]

