<template>
<div class="report-page">
    <div class="w" id="pdfDom">
        <img class="report-page-top" :src="topImgUrl">
        <!-- <div class="report-view" id="exportPdf" ref="exportPdf"> -->
        <div class="report-view" ref="exportPdf">
            <div class="report-view-title">私有云部署方案</div>
            <div class="report-page-container">
                <div class="spliter"></div>
                <div class="private-cloud-section-title">私有云业务</div>
                <div class="private-cloud-business">
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">OA办公系统</div>
                    </div>
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">虚拟桌面</div>
                    </div>
                    <div class="business">
                        <img class="business-icon" :src="bussinessUrl">
                        <div class="business-content">OERP企业咨询计划</div>
                    </div>
                </div>
                <div class="spliter"></div>
                <div class="private-cloud-section-title">私有云硬件配置情况</div>
                <table class="report-table-view" cellpadding="10">
                    <tr bgcolor="#F1F3F9" width="300px">
                        <td>CPU型号：{{CPUType}}</td>
                        <td>CPU数量：{{CPUNum}}</td>
                    </tr>
                    <tr width="300px">
                        <td>内存规格：{{RAMType}}</td>
                        <td>内存数量：{{RAMNum}} </td>
                    </tr>
                    <tr bgcolor="#F1F3F9" width="300px">
                        <td>存储类型：{{storageType}}</td>
                        <td>存储容量：{{storageCapacity}}</td>
                    </tr>
                    <tr width="300px">
                        <td>网络类型：{{netType}} </td>
                        <td>网卡数量：{{netNum}}</td>
                    </tr>
                </table>
                <div class="private-cloud-section-title">企业IT 三年规划</div>
                <div class="company-plan">
                    <div class="plan-view">
                        <div class="plan-view-title">扩容方案</div>
                        <div class="plan-view-content">根据数据年增长率20%计算， 需新增节点 *， 即超融合服务器*台， 耗电量增加*W， 新增UPS设备 * 台。 </div>
                    </div>
                    <div class="plan-view">
                        <div class="plan-view-title">网络方案</div>
                        <div class="plan-view-content">根据数据年增长率20%计算，预计带宽为 *M, 现有带宽满足需求。 </div>
                    </div>
                </div>

            </div>

        </div>
        <div class="export-report-btn" @click="savePdf()">导出报告</div>
    </div>
</div>
</template>

<script>
import {
    adminGetReport
} from '../../api'
export default {
    data() {
        return {
            topImgUrl: require('../../assets/images/reportTop.png'),
            bussinessUrl: require('../../assets/images/reportView.png'),
            CPUType: '',
            CPUNum: '',
            RAMType: '',
            RAMNum: '',
            storageType: '',
            storageCapacity: '',
            netType: '',
            netNum: '',
            htmlTitle: "report", //这个是pdf文件的名字
        }
    },
    mounted() {
        this.getReportInfo()
    },

    beforeDestroy() {
        delete this.$route.query.case_id
    },
    methods: {
        getReportInfo() {
            adminGetReport({
                params: {
                    case_id: this.$route.query.case_id
                },
                success: res => {
                    this.CPUType = res.data.cpu_type
                    this.CPUNum = res.data.cpu_count
                    this.RAMType = res.data.memory_type
                    this.RAMNum = res.data.memory_count
                    this.storageType = res.data.storage_type
                    this.storageCapacity = res.data.storage_count
                    this.netType = res.data.network_type
                    this.netNum = res.data.network_count
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    console.log(err)
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        savePdf() {
            this.$PDFSave(this.$refs.exportPdf, "我的文件");
        },
    }
}
</script>

<style>
.report-page {
    background-image: url("../../assets/images/qabackground.png");
    background-size: 100% 100%;
    height: 100%;
    width: 100%;
    display: flex;
    /* position: fixed; */
    overflow-y: auto;
}

.report-page-top {
    background-size: 100% 100%;
    padding-top: 10px;
    height: 60px;
    width: 100%;
    /* position: relative;
    z-index: 100; */
}

.report-view {
    background-color: #fff;
    width: 80%;
    /* box-shadow: 0 0 5px 1px #c9d1da; */
    box-shadow: 0px 5px 16px 5px rgba(217, 223, 232, 1);
    margin-left: 10%;
    margin-top: -30px;
    /* margin-bottom: 50px; */
    position: relative;
    z-index: 1000;
}

.report-view-title {
    height: 50px;
    font-size: 24px;
    line-height: 30px;
    padding-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.report-page-container {
    width: 95%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}

.spliter {
    width: 100%;
    height: 1px;
    background: rgba(204, 204, 204, 1);
    margin: 10px auto;
}

.private-cloud-section-title {
    width: 100%;
    height: 30px;
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    margin: 10px 0;
}

.private-cloud-business {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.business {
    width: calc((100% - 150px)/3);
    height: 70px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    /* box-shadow: 0px 2px 10px 2px rgba(217, 223, 232, 0.31);*/
    box-shadow: 0 0 5px 1px #c9d1da;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 10px;
    border-radius: 3px;
}

.business-icon {
    float: left;
    margin: 10px 10px;
}

.business-content {
    height: 70px;
    line-height: 70px;
    display: flex;
}

.report-table-view {
    margin-left: 20px;
}

.zebra-crossing {
    width: 100%;
    height: 180px;
    background: linear-gradient(to bottom, #F1F3F9 50%, #fff 0%);
    background-size: 100% 50px;
}

.company-plan {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
}

.plan-view {
    width: calc((100% - 80px)/2);
    box-shadow: 0 0 5px 1px #c9d1da;
    margin: 0 auto;
}

.plan-view-title {
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
    background: rgba(73, 89, 118, 1);
    margin: 0 auto;
    color: #fff;
    border-radius: 3px;
}

.plan-view-content {
    margin: 10px 20px;
}

.export-report-btn {
    width: 181px;
    height: 40px;
    line-height: 40px;
    background: rgba(4, 122, 255, 1);
    border-radius: 3px;
    color: #fff;
    text-align: center;
    margin: 20px auto;
    cursor: pointer;
}
</style>
