<template>
<div class="adminLogin-page">
    <div class="background-page">
        <div class="background-page-logo"></div>
    </div>
    <div class="admin-login-view" id="loginview" style="width:380px">
        <img :src="loginTitle" class="admin-login-view-title">
        <div class="admin-login-input">
            <div class="admin-login-input-tips">账号：</div>
            <input class="admin-login-input-box" placeholder="账号" v-model="userName">
        </div>
        <div class="admin-login-input">
            <div class="admin-login-input-tips">密码：</div>
            <input class="admin-login-input-box" placeholder="密码" v-model="password" type="password" @keyup.enter="submitLogin">
        </div>
        <div class="admin-login-submit-btn" @click="submitLogin">登录</div>
    </div>
    <div class="current-version" id="version" style="left: 47%;">当前版本：20200805V1.0</div>
</div>
</template>

<script>
import {
    login
} from '../../api'
export default {
    mounted() {
        this.$nextTick(() => {
            if (document.documentElement.clientWidth && document.documentElement.clientWidth > 1400) {
                document.getElementById("loginview").style.width = '610px'
                document.getElementById("version").style.left = '48%'

            } else {
                document.getElementById("loginview").style.width = '440px'
                document.getElementById("version").style.left = '47%'
            }
        })
    },
    methods: {
        submitLogin() {
            if (this.userName && this.password) {
                login({
                    params: {
                        "password": this.userName,
                        "username": this.password
                    },
                    success: res => {
                        console.log(res)
                        this.$router.push({
                            name: 'console'
                        })
                    },
                    fail: res => {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    },
                    error: err => {
                        console.log(err)
                        this.$message({
                            message: '服务器异常',
                            type: 'warning'
                        })
                    }
                })
            } else {
                this.$message({
                    message: '请登录',
                    type: 'warning'
                })
            }
        }
    },
    data() {
        return {
            userName: '',
            password: '',
            loginTitle: require("../../assets/images/backcontrol/login/水印.png"),
        }
    }
}
</script>

<style scoped>
.adminLogin-page {
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.background-page {
    width: 100%;
    height: 50%;
    background-image: url("../../assets/images/backcontrol/login/背景.png");
    background-size: 100% 100%;
    position: fixed;
    top: 0;
    z-index: 1000;
}

.background-page-logo {
    background-image: url("../../assets/images/backcontrol/login/登录-LOGO.png");
    width: 15.52%;
    height: 3.63%;
    /* width: 298px;
    height: 34px; */
    background-size: 100% 100%;
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 50000;
}

.admin-login-view {
    /* width: 35%; */
    height: auto;
    margin: 0 auto;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 30px;
    position: fixed;
    /* top: 18%; */
    margin: auto 0;
    left: 35%;
    z-index: 5000;
    box-shadow: 0px 0px 1px 2px #dedede;
    border-radius: 5px;
}

.admin-login-view-title {
    width: 46.85%;
    margin-top: 3%;
    margin-left: 3%;
}

.admin-login-input {
    width: 100%;
    height: auto;
    margin: 0 25px;
    margin-bottom: 20px;
}

.admin-login-input-tips {
    font-weight: 500;
    font-size: 12px;
    padding-bottom: 10px;
    color: #232323;
}

.admin-login-input-box {
    width: 90%;
    height: 50px;
    border: 1px solid #a5a5a5;
    margin-bottom: 3%;
}

.admin-login-submit-btn {
    width: 80%;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
    /* margin-top: 20px; */
    margin-bottom: 5%;
    background-color: #1a92ff;
    color: #fff;
    text-align: center;
    cursor: pointer;
    letter-spacing: 1px;
}

.admin-login-submit-btn:hover {
    background-color: #48a8ff;
}

.admin-login-submit-btn:active {
    background-color: #1b78cc;
}

.current-version {
    position: fixed;
    bottom: 10%;
  
    font-size: 12px;
}

input:focus {
    outline: 1px solid #1a92ff;
}

input::-webkit-input-placeholder {
    color: #6d6d6d;
    font-size: 12px;
}
</style>
